import * as React from "react";
import {
  Row,
  Col,
  Button,
  Input,
  Switch,
  Radio,
  Divider,
  Select,
  Collapse,
  Icon
} from "antd";
import "./../style.css";
import FileUpload from "./../../Categories/Components/FileUpload";

const { Option } = Select;
const { Panel } = Collapse;

export const DetailsForm = ({ formDetails, onChange, title, description }) => {
  return (
    <div>
      <Row className="marginBottom20px">
        <Col span={18}>
          <h1>{title}</h1>
          <div>{description}</div>
        </Col>
      </Row>
      <Row className="marginBottom20px">
        <Col span={12}>
          <div className="marginBottom20px">
            <div className="marginBottom10px">
              Combo name<span className="requiredFieldRedColor">*</span>
            </div>
            <Input
              size="large"
              placeholder="Item name"
              value={formDetails.name}
              onChange={(e: any) => {
                formDetails.name = e.target.value;
                onChange("formDetails", formDetails);
              }}
            />
          </div>
        </Col>
        <Col span={12}>
          <div className="marginBottom20px">
            <div className="marginBottom10px">
              Image link<span className="requiredFieldRedColor">*</span>
            </div>
            <Input
              size="large"
              placeholder="Image Link"
              value={formDetails.imageUrl}
              onChange={(e: any) => {
                formDetails.imageUrl = e.target.value;
                onChange("formDetails", formDetails);
              }}
            />
          </div>
        </Col>
      </Row>

      <Row className="marginBottom20px">
        <Col span={12}>
          <div className="marginBottom20px">
            <div className="marginBottom10px">Description</div>
            <Input.TextArea
              rows={4}
              placeholder="Description"
              value={formDetails.description}
              onChange={(e: any) => {
                formDetails.description = e.target.value;
                onChange("formDetails", formDetails);
              }}
            />
          </div>
        </Col>
        <Col span={12}>
          <div className="marginBottom20px displayFlex ">
            <div
              style={{
                width: 100,
                height: 100,
                marginRight: 20
              }}
            >
              {formDetails.imageUrl == "" && (
                <img
                  className="catImage"
                  src={require("./../../Categories/Assets/add_image.png")}
                />
              )}
              {formDetails.imageUrl != "" && (
                <img className="catImage" src={formDetails.imageUrl} />
              )}
            </div>

            <FileUpload
              uiType={"categoryManagement"}
              availableImage={0}
              onImageUpload={this.handleUploadedImage}
              title="Upload Product Image"
            />
          </div>
          <Col className="imageDesc">* Maximum file size allowed is 1 MB</Col>
        </Col>
      </Row>

      <Row className="marginBottom20px">
        <Col span={12}>
          <div className="marginBottom20px">
            <div className="marginBottom10px">
              Nutrition value<span className="requiredFieldRedColor">*</span>{" "}
              (calories)
            </div>
            <Input
              size="large"
              placeholder="Nutrition value"
              value={
                formDetails.extend && formDetails.extend.extend_nutrition_value
              }
              onChange={(e: any) => {
                formDetails.extend.extend_nutrition_value = e.target.value;
                onChange("formDetails", formDetails);
              }}
            />
          </div>
        </Col>
        <Col span={12}>
          <div className="marginBottom20px">
            <div className="marginBottom10px">
              Tag: Veg / Non-Veg
              <span className="requiredFieldRedColor">*</span>
            </div>
            <Radio.Group
              value={
                formDetails.extend && formDetails.extend.extend_veg_non_veg
                  ? formDetails.extend.extend_veg_non_veg
                  : null
              }
              onChange={e => {
                if (!formDetails.extend) {
                  formDetails.extend = {};
                }
                formDetails.extend.extend_veg_non_veg = e.target.value;
                onChange("formDetails", formDetails);
              }}
              buttonStyle="solid"
            >
              <Radio.Button value="veg">Veg</Radio.Button>
              <Radio.Button value="nonVeg">Non-veg</Radio.Button>
            </Radio.Group>
          </div>
        </Col>
      </Row>

      <Row className="marginBottom20px">
        <Col span={12}>
          <div className="marginBottom20px">
            <div className="marginBottom10px">
              Items preparation time (minutes)
            </div>
            <Input
              size="large"
              placeholder="Items preparation time"
              value={
                formDetails.extend &&
                formDetails.extend.extend_item_preparation_time
              }
              onChange={(e: any) => {
                formDetails.extend.extend_item_preparation_time =
                  e.target.value;
                onChange("formDetails", formDetails);
              }}
            />
          </div>
        </Col>
      </Row>

      <Divider className="marginBottom20px" />
    </div>
  );
};

export const ComboPricing = ({
  description,
  title,
  pricingDetails,
  onChange,
  storeFormatArray,
  taxLevels
}) => {
  return (
    <div>
      <Row className="marginBottom20px">
        <Col span={18}>
          <h1>{title}</h1>
          <div>{description}</div>
        </Col>
      </Row>
      {storeFormatArray.length &&
        storeFormatArray.map((storeFormat, index) => {
          return (
            <Row className={"marginBottom40px "}>
              <Col span={18} className="alignSelfCenter width150px fontBold">
                {`${storeFormat}:`}
              </Col>
              <div className="alignCenter marginRight10px">
                <Col className="alignSelfCenter marginRight10px">Price</Col>
                <Col className="alignSelfCenter">
                  <Input size="large" placeholder="Price" />
                </Col>
              </div>
              <div className="alignCenter marginRight10px">
                <Col className="alignSelfCenter marginRight10px">Tax</Col>
                <Col className="alignSelfCenter">
                  <Select size="large" style={{ width: 200 }} placeholder="Tax">
                    {taxLevels.map((taxLevel, taxLevelIndex) => {
                      return (
                        <Option key={taxLevelIndex} value={taxLevel.name}>
                          {taxLevel.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Col>
              </div>
            </Row>
          );
        })}
      <Divider className="marginBottom20px" />
    </div>
  );
};

export const ComboItems = ({
  description,
  title,
  comboItemsDetails,
  onChange
}) => {
  return (
    <div>
      <Row className="marginBottom20px">
        <Col span={18}>
          <h1>{title}</h1>
          <div>{description}</div>
        </Col>
      </Row>
      {comboItemsDetails.map((item, comboItemIndex) => {
        return (
          <div
            key={`combo-items-${comboItemIndex}`}
            className={" marginLeft40px"}
          >
            <div className="marginBottom20px flexWrapper">
              <div>
                <h2>Type {comboItemIndex + 1}</h2>
                <div>
                  Add item selections or categories that user can select from,
                  like any veg-pizza
                </div>
              </div>
              <div>
                <Icon
                  type="delete"
                  theme="filled"
                  style={{ fontSize: "18px", color: "grey" }}
                  onClick={() => {
                    comboItemsDetails.splice(comboItemIndex, 1);
                    onChange("comboItemsDetails", comboItemsDetails);
                  }}
                />
              </div>
            </div>

            <Row className={"marginBottom40px "}>
              <Col span={8}>
                <div className="flexWrapper width250px">
                  <div className="alignSelfCenter marginRight10px">
                    Categories
                  </div>
                  <div className="alignSelfCenter">
                    <Select
                      size="large"
                      style={{ width: 200 }}
                      placeholder="Tax"
                    >
                      {["1", "2", "3"].map((taxLevel, taxLevelIndex) => {
                        return (
                          <Option
                            key={`tax-level-${taxLevelIndex}`}
                            value={taxLevel}
                          >
                            {taxLevel}
                          </Option>
                        );
                      })}
                    </Select>
                  </div>
                </div>
              </Col>
              <Col span={8}>
                <div className="flexWrapper width250px">
                  <div className="alignSelfCenter marginRight10px">
                    Attribute 1 (size)
                  </div>
                  <div className="alignSelfCenter">
                    <Select
                      size="large"
                      style={{ width: 200 }}
                      placeholder="Tax"
                    >
                      {["1", "2", "3"].map((taxLevel, taxLevelIndex) => {
                        return (
                          <Option key={taxLevelIndex} value={taxLevel}>
                            {taxLevel}
                          </Option>
                        );
                      })}
                    </Select>
                  </div>
                </div>
              </Col>
              <Col span={8}>
                <div className="flexWrapper width250px">
                  <div className="alignSelfCenter marginRight10px">
                    Attribute 2 (crust)
                  </div>
                  <div className="alignSelfCenter">
                    <Select
                      size="large"
                      style={{ width: 200 }}
                      placeholder="Tax"
                    >
                      {["1", "2", "3"].map((taxLevel, taxLevelIndex) => {
                        return (
                          <Option key={taxLevelIndex} value={taxLevel}>
                            {taxLevel}
                          </Option>
                        );
                      })}
                    </Select>
                  </div>
                </div>
              </Col>
            </Row>
            <Divider className="marginBottom20px" />
          </div>
        );
      })}
      <Button
        disabled={false}
        className="addNewItemBtn"
        onClick={() => {
          comboItemsDetails.push({});
          onChange("comboItemsDetails", comboItemsDetails);
        }}
        type="ghost"
        size="large"
        loading={false}
      >
        ADD NEW
      </Button>
      <Divider className="marginBottom20px" />
    </div>
  );
};
