import * as React from "react";
import {
  Row,
  Col,
  Button,
  Input,
  Switch,
  Radio,
  Divider,
  Select,
  Collapse,
  Icon
} from "antd";
import "./style.css";
import {
  DetailsForm,
  ComboPricing,
  ComboItems
} from "./components/detailsForm";
import { withApollo, ApolloProviderProps } from "react-apollo";

import { GET_BUSINESS_RULE } from "./../../PlatformQueries";

const { Option } = Select;
const { Panel } = Collapse;

const ADDITIONS = [
  { name: "Black Olives" },
  { name: "Chicken Fajita" },
  { name: "Garlic" },
  { name: "Green Chilli" },
  { name: "Green Peppers" },
  { name: "Ground Beef" },
  { name: "Mushrooms" },
  { name: "Pepperoni" },
  { name: "Roasted Turkey" },
  { name: "Shrimp" },
  { name: "Smokey Beef Bacon" },
  { name: "Tomato" }
];

const TAX = [
  { name: "F&B Tax" },
  { name: "DELIVERY" },
  { name: "AIRPORT SURCHARGE" }
];

interface iProps extends ApolloProviderProps<any> {}

interface iState {
  formDetails: any;
  comboPricingDetails: any;
  comboItemsDetails: any;
  storeFormatArray: any;
}

class CreateForm extends React.Component<iProps, iState> {
  constructor(props: iProps) {
    super(props);
    this.state = {
      formDetails: {
        code: "",
        name: "",
        description: "",
        organizationId: "",
        imageUrl: "",
        type: "REGULAR",
        status: "ACTIVE",
        categoryIds: [],
        optionIds: [],
        variants: [],
        extend: {}
      },
      comboPricingDetails: {},
      comboItemsDetails: [],
      storeFormatArray: []
    };
  }

  UNSAFE_componentWillMount() {
    this.props.client
      .query({
        query: GET_BUSINESS_RULE,
        variables: { id: "1" },
        fetchPolicy: "network-only"
      })
      .then(res => {
        let storeFormatArray = res.data.businessRule.ruleDefaultValue.split(
          ","
        );
        this.setState({ storeFormatArray });
      })
      .catch(err => {
        console.log("Failed to get store format" + err);
      });
  }

  handleChange = (type, value) => {};

  onChange = (type: string, value: any) => {
    this.setState((prevState: Readonly<iState>, props: Readonly<iProps>) => {
      return {
        ...prevState,
        [type]: value
      };
    });
  };

  handleUploadedImage = val => {
    const { formDetails } = this.state;
    formDetails.imageUrl = val[0].url;
    this.setState({ formDetails });
  };

  isVariantStatusIsActive = variant => {
    let { formDetails } = this.state;

    return (
      formDetails.extend &&
      formDetails.extend.extend_price_tax &&
      formDetails.extend.extend_price_tax[variant.sku] &&
      formDetails.extend.extend_price_tax[variant.sku].dineIn.price &&
      formDetails.extend.extend_price_tax[variant.sku].delivery.price &&
      formDetails.extend.extend_price_tax[variant.sku].transportHub.price
    );
  };

  onSave = () => {};

  render() {
    let { formDetails, comboItemsDetails } = this.state;
    console.log(this.state);

    return (
      <div className="comboFormContainer">
        <Row className=" marginBottom20px">
          <Col span={14}>
            <h1>Combos:</h1>
          </Col>
          <Col span={4} className="alignSelfCenter">
            <div className="flexWrapper width100px ">
              <Switch
                checked={formDetails.status === "ACTIVE"}
                onChange={(value: any) => {
                  formDetails.status = value ? "ACTIVE" : "INACTIVE";
                  this.onChange("formDetails", formDetails);
                }}
              />
              <div className="alignSelfCenter marginLeft10px">
                {formDetails.status}
              </div>
            </div>
          </Col>
          <Col span={2} className="alignSelfCenter">
            <Icon
              type="delete"
              theme="filled"
              style={{ fontSize: "18px", color: "grey" }}
              onClick={() => {
                // formDetails.variants.splice(index, 1);
                // this.onChange("formDetails", formDetails);
              }}
            />
          </Col>
          <Col span={2}>
            <Button
              disabled={false}
              className="saveBtn button"
              type="ghost"
              size="large"
              onClick={() => {
                this.onSave();
              }}
              loading={false}
            >
              SAVE
            </Button>
          </Col>
        </Row>
        {/* <Divider className="marginBottom20px" /> */}
        <DetailsForm
          formDetails={formDetails}
          onChange={this.onChange}
          title="Combo Description"
          description="Combo name, description, images etc"
        />
        <ComboPricing
          title="Combo Pricing"
          description="Combo price, tax etc per menu type."
          onChange={this.onChange}
          pricingDetails={{ test: "test" }}
          storeFormatArray={this.state.storeFormatArray}
          taxLevels={TAX}
        />
        <ComboItems
          title="Combo Items"
          description="Items the combos consists of including any side it may have."
          onChange={this.onChange}
          comboItemsDetails={comboItemsDetails}
        />
      </div>
    );
  }
}

export default withApollo(CreateForm);
