import * as React from "react";
import { Row, Col, Cascader, Icon, Spin, message } from "antd";
import * as jwt from "jsonwebtoken";
import { withApollo, ApolloProviderProps } from "react-apollo";

import "./style.css";
import VariantDetailsForm from "./variantDetailsForm";
import {
  OPTIONS,
  GET_PH_CATEGORIES,
  CREATE_PRODUCT
} from "./../../PlatformQueries";
import { History } from "history";

interface iProps extends ApolloProviderProps<any> {
  history: History;
}

interface iState {
  processedCategoryList: any;
  categoryRawData: any;
  productsRawData: any;
  isFetching: boolean;
  isCategorySelected: boolean;
  searchInput: any;
  organizationId: string;
  rawOptions: any;
  finalOptions: any;
  productId: String;
  isSaving: boolean;
}

class CreateItem extends React.Component<iProps, iState> {
  constructor(props: iProps) {
    super(props);
    this.state = {
      processedCategoryList: [],
      categoryRawData: [],
      productsRawData: [],
      isFetching: true,
      isCategorySelected: false,
      searchInput: null,
      organizationId: "",
      rawOptions: [],
      finalOptions: {},
      productId: "",
      isSaving: false
    };
  }

  UNSAFE_componentWillMount() {
    this.getCategories();
    this.getOptions();
  }

  getCategories = () => {
    const jwtToken = localStorage.getItem("jwt");
    const { org_id }: any = jwt.decode(jwtToken);

    if (org_id) {
      this.props.client
        .query({
          query: GET_PH_CATEGORIES,
          variables: { catalogId: "2", categoryCode: "PH_SQUARE_1" },
          fetchPolicy: "network-only"
        })
        .then(res => {
          var final = this.processData(res.data.categoriesWithChildren);
          this.setState({
            processedCategoryList: final,
            categoryRawData: res.data.categoriesWithChildren,
            isFetching: false,
            organizationId: org_id
          });
        })
        .catch(err => {
          console.log("Failed to get Category Details" + err);
        });
    } else {
      console.log("Error getting JwtData");
    }
  };

  getOptions = () => {
    this.props.client
      .query({
        query: OPTIONS,
        fetchPolicy: "network-only"
      })
      .then(res => {
        let rawOptions = res.data.options;
        let finalOptions = {};
        let optionValues;

        rawOptions.map((option, index) => {
          optionValues = [];
          option.optionValues.map((optionValue, index) => {
            optionValues.push({ id: optionValue.id, name: optionValue.value });
          });
          finalOptions[option.name] = optionValues;
        });

        this.setState({
          rawOptions,
          finalOptions
        });
      })
      .catch(err => {
        console.log("Failed to get option" + err);
      });
  };

  processData(data) {
    var arr = [];
    var el = data.children;
    el.forEach(element => {
      var individual = {
        id: element.id,
        name: element.name,
        value: element.name,
        label: element.name,
        description: element.description,
        status: element.status,
        children: []
      };
      if (element.children !== undefined) {
        individual.children =
          element.children.length > 0 ? this.processData(element) : [];
      }
      arr.push(individual);
    });
    return arr;
  }

  onChangeCascader = (value, selectedOptions) => {
    if (value.length === 0 && selectedOptions.length === 0) {
      this.setState({
        isCategorySelected: false,
        productsRawData: []
      });
    }
    if (selectedOptions[selectedOptions.length - 1].children.length === 0) {
      let productId = selectedOptions[selectedOptions.length - 1].id;
      this.setState({
        isCategorySelected: true,
        productId
      });
    }
  };

  onChangeTable = (pagination, filters, sorter, extra) => {
    console.log("params", pagination, filters, sorter, extra);
  };

  onChange = (type, value) => {
    this.setState((prevState: Readonly<iState>, props: Readonly<iProps>) => {
      return {
        ...prevState,
        [type]: value
      };
    });
  };

  displayRender = (labels, selectedOptions) => {
    if (labels.length === 1) {
      return <span key={selectedOptions[0].value}>All / {labels[0]} </span>;
    } else {
      return labels.map((label, i) => {
        const option = selectedOptions[i];
        if (i === labels.length - 1) {
          return <span key={option.value}>{label}</span>;
        }
        return <span key={option.value}>All / {label} / </span>;
      });
    }
  };

  onSaveParentDetails = input => {
    let parentDetails = input;
    parentDetails.organizationId = this.state.organizationId;
    parentDetails.optionIds = [1, 2];
    parentDetails.categoryIds = [this.state.productId];
    parentDetails.code = `U${Math.floor(100000 + Math.random() * 900000)}`;

    parentDetails.variants.map((variant, index) => {
      variant.optionValueIds = [];
      variant.optionValues.map((optionValue, optionValueIndex) => {
        variant.optionValueIds.push(optionValue.id);
      });
      delete variant["optionValues"];
    });
    delete parentDetails["__typename"];
    parentDetails.extend = JSON.stringify(parentDetails.extend);
    this.setState(
      {
        isSaving: true
      },
      () => {
        this.props.client
          .mutate({
            mutation: CREATE_PRODUCT,
            variables: { input: parentDetails }
          })
          .then(res => {
            message.success("Successfully updated");
            this.setState({ isSaving: false });
            this.props.history.push("/core/item-management/list");
          });
      }
    );
  };

  showProductDetailsForm = () => {
    let productParentDetails = {
      code: "",
      name: "",
      description: "",
      organizationId: "",
      imageUrl: "",
      type: "REGULAR",
      status: "ACTIVE",
      categoryIds: [],
      optionIds: [],
      variants: [],
      extend: {}
    };
    return (
      <VariantDetailsForm
        productParentDetails={productParentDetails}
        onSaveParentDetails={this.onSaveParentDetails}
        finalOptions={this.state.finalOptions}
        isSaving={this.state.isSaving}
      />
    );
  };

  render() {
    let { processedCategoryList, isFetching, isCategorySelected } = this.state;

    return (
      <div className="itemsManagementContainer">
        <Row>
          <Col span={1} className="alignSelfCenter">
            <div
              onClick={() => {
                this.props.history.push("/core/item-management/list");
              }}
              className="cursorPointer webhookBackButton alignSelfCenter"
            >
              <Icon type="arrow-left" style={{ fontSize: 20 }} />
            </div>
          </Col>
          <Col span={18}>
            <h1 style={{ marginBottom: 0 }}>Item Management / New Item</h1>
          </Col>
        </Row>

        {isFetching && processedCategoryList.length === 0 ? (
          <Col className="itemManagementBodyWrapper alignCenter">
            <Spin size="large" />
          </Col>
        ) : (
            <Col className="itemManagementBodyWrapper">
              <Row className="marginBottom20px">
                <Col span={10}>
                  <div className="marginBottom10px">Please choose a category</div>
                  <Cascader
                    size="large"
                    options={processedCategoryList}
                    onChange={(val, selectedOptions) => {
                      this.onChangeCascader(val, selectedOptions);
                    }}
                    displayRender={this.displayRender}
                    style={{ width: "100%" }}
                    changeOnSelect
                  />
                </Col>
              </Row>
            </Col>
          )}
        {isCategorySelected && this.showProductDetailsForm()}
      </div>
    );
  }
}

export default withApollo(CreateItem);
